.card-product {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    &__slider {
        max-width: 38.7%;
        width: 100%;
        &-inner {
            padding: 15px 15px 30px;
            border: 1px solid var(--Gainsboro);
        }
        .card-img {
            height: 100%;
            margin-bottom: 0;
        }
        .gallery-top {
            height: 352px;
            .swiper-slide {
                padding: 18px 35px 0;
            }
        }
        .gallery-thumbs {
            height: 76px;
            margin-top: 20px;
            .card-img {
                width: 76px;
            }
            .swiper-wrapper {
                justify-content: center;
            }
            .swiper-slide {
                border: 1px solid var(--Gainsboro);
                cursor: pointer;
                transition: all 0.2s;
                &:hover {
                    border: 1px solid var(--Wedgewood);
                }
                &:last-child {
                    margin-right: 0 !important;
                }
            }
            .swiper-slide-thumb-active {
                border: 1px solid var(--Wedgewood);
            }
        }
        .badge {
            left: 0;
            &.first {
                top: 0;
            }
            &.second {
                top: 32px;
            }
            &.third {
                top: 65px;
            }
        }
        .swiper-button-prev::after, .swiper-button-next::after {
            font-size: 28px;
        }
        .swiper-button-prev {
            left: 0;
        }
        .swiper-button-next {
            right: 0;
        }
        .bx-e10, .bx-e11 {
            top: calc(50% - 14px);
        }
        .swiper-button-prev, .swiper-button-next {
            top: calc(50% + 2px);
        }
        .swiper-button-prev, .swiper-button-next, .swiper-button-prev::after, .swiper-button-next::after {
            width: 21px;
        }
    }
    &__page {
        .sorting__wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 40px;
            h1 {
                margin-bottom: 0;
            }
        }
        .feedback {
            max-width: calc(61.3% - 50px);
            width: 100%;
            margin-left: calc(38.7% + 50px);
            margin-top: -92px;
            h2 {
                font-size: 20px;
                text-align: left;
            }
            input {
                width: 276px;
            }
        }
        .feedback__inner .input-wrap:not(:last-of-type) {
            margin-right: 10px;
        }
        .feedback__inner .Btn {
            margin-left: 10px;
            padding: 8px 38px;
            flex-shrink: 0;
        }
        .feedback-brims {
            margin-left: 15px;
        }
        .card-Item[data-card="list"] {
            .card-full {
                &>.Instock {
                    display: none;
                }
            }
            .card-img {
                height: 135px;
                width: 240px;
                flex-shrink: 0;
            }
            .card-action {
                margin-top: 47.5px;
                align-self: start;
            }
            .card-full .card-body__wrap {
                width: auto;
                flex-grow: 1;
                flex-wrap: wrap;
            }
            .card-full .card-body {
                max-width: 944px;
                width: 100%;
                justify-content: space-between;
                order: 1;
            }
            .card-full .card-link {
                max-width: 800px;
                width: 100%;
                &:after {
                    display: none;
                }
            }
            .card-full .card-bottom {
                max-width: 455px;
                width: 100%;
                height: 135px;
                flex-shrink: 0;
                justify-content: space-between;
                order: 2;
            }
            .count__wrap {
                margin: 0;
                .price {
                    display: none;
                }
            }
            .card-body__info {
                flex-shrink: 1;
                span {
                    display: inline-block;
                    font-weight: 600;
                    margin-bottom: 20px;
                    color: var(--CodeColor);
                }
                button {
                    margin-top: 20px;
                    font-size: 14px;
                    font-weight: 600;
                    color: var(--Charcoal);
                    border-bottom: 1px dashed var(--Charcoal);
                    transition: all 0.2s;
                    &:hover {
                        border-bottom: 1px solid white;
                    }
                }
            }
        }
        .card-Item__table {
            display: none;
            width: calc(100% + 10px);
            margin-left: -10px;
            margin-top: 20px;
            order: 3;
            &>div {
                display: flex;
            }
            table:first-child {
                margin-right: 40px;
            }
            table {
                width: 50%;
            }
            button {
                margin-top: 10px;
                margin-left: 10px;
                font-size: 14px;
                font-weight: 600;
                color: var(--Charcoal);
                border-bottom: 1px dashed var(--Charcoal);
                transition: all 0.2s;
                &:hover {
                    border-bottom: 1px solid white;
                }
            }
            td, th {
                padding-right: 25px;
            }
        }
        &-bottom {
            overflow: hidden;
        }
        .text-block {
            margin-top: 40px;
        }
        .tab-containers[data-tab="card-tab"] {
            .card-Item:not([data-card="list"]) {
                flex: 0 0 20%;
                max-width: 20%;
            }
        }
    }
    &__characteristics {
        max-width: 34.7%;
        width: 100%;
        padding-left: 40px;
        padding-right: 50px;
        margin-bottom: 129px;
        &-title {
            display: flex;
            justify-content: space-between;
            margin-bottom: 15px;
            padding-left: 10px;
            p {
                margin: 0;
                font-size: 13px;
                font-weight: 700;
                text-transform: uppercase;
            }
            img {
                height: 20px;
            }
        }
        &-table {
            width: 100%;
            table-layout: fixed;
            border-collapse:collapse;
            tr {
                transition: all 0.2s;
                &:hover {
                    background-color: var(--tableBg);
                }
            }
            td, th {
                width: 100%;
                text-align: left;
                padding: 3px 10px 7px;
                line-height: 23px;
                vertical-align: top;
            }
        }
        &-all {
            display: none;
        }
        button {
            margin: 25px 0 0 10px;
            border-bottom: 1px dashed var(--Charcoal);
            font-size: 14px;
            color: var(--Charcoal);
            transition: all 0.2s;
            &:hover {
                border-bottom: 1px dashed white;
            }
        }
    }
    &__action {
        background-color: var(--tableBg);
        padding: 30px 28px 35px;
        &-block {
            max-width: 26.6%;
            width: 100%;
            margin-bottom: 129px;
            &inner {
                padding: 30px;
                border: 1px solid var(--Gainsboro);
            }
        }
        &-delivery {
            display: flex;
            justify-content: space-between;
            margin-bottom: 25px;
            .icon-6-7_delivery {
                margin-right: 10px;
                margin-top: -5px;
                font-size: 26px;
            }
            &>span {
                display: flex;
                align-items: flex-start;
                flex-shrink: 1;
                font-size: 13px;
                font-weight: 600;
                padding-right: 90px;
                i {
                    display: inline-block;
                }
                span {
                    display: inline-block;
                    width: 100%;
                }
            }
            .popup-desc {
                left: -51px;
                white-space: nowrap;
                font-weight: normal;
                font-family: "Arial", sans-serif;
                &:after {
                    left: 70px;
                }
            }
        }
        .badge {
            position: static;
            display: inline-block;
            padding: 0;
            margin-top: 15px;
        }
        .price {
            font-size: 18px;
        }
        .count {
            background-color: var(--White);
        }
        &>div:first-child {
            display: flex;
            justify-content: space-between;
        }
        &-btn {
            margin-top: 30px;
            display: flex;
            justify-content: space-between;
            button {
                max-width: 48.5%;
                width: 100%;
                flex-shrink: 0;
                &:focus {
                    outline: none;
                }
            }
            .basketBtn {
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 14px;
                i {
                    font-size: 23px;
                }
                span:first-child {
                    display: inline-block;
                }
            }
        }
        &-otherbtn {
            display: flex;
            margin-top: 30px;
            i {
                font-size: 20px;
                margin-right: 10px;
            }
            button {
                display: flex;
                align-items: center;
                font-size: 14px;
                font-weight: 600;
                transition: all 0.2s;
                &:hover {
                    color: var(--Flamenco);
                }
                &:first-child {
                    margin-right: 40px;
                }
            }
            span {
                display: inline-block;
            }
        }
    }
    &__tabs {
        margin-bottom: 40px;
        padding: 30px 0 15px;
        border-bottom: 1px solid var(--Gainsboro);
        .tab-btn {
            position: relative;
            padding: 0;
            color: var(--Wedgewood);
            font-size: 17px;
            font-weight: 700;
            border: none;
            overflow: visible;
            &:not(:last-child) {
                margin-right: 40px;
            }
            &:after {
                content: "";
                position: absolute;
                display: block;
                bottom: -17px;
                left: 0;
                height: 3px;
                width: 100%;
                background-color: var(--Flamenco);
                opacity: 0;
                transition: all 0.2s;
            }
            &:hover {
                &:after {
                    opacity: 1;
                }
            }
        }
        .tab-btn.active:after {
            opacity: 1;
        }
    }
    &__tabslider,&__tabslider1 {
        overflow: visible;
        .swiper-button-prev::after, .swiper-button-next::after {
            font-size: 28px;
        }
        .swiper-button-prev {
            left: 0;
        }
        .swiper-button-next {
            right: 0;
        }
        .card-link {
            overflow: hidden;
            height: 44px;
        }
        .swiper-button-prev::after, .swiper-button-next::after, .swiper-button-prev, .swiper-button-next {
            width: 21px;
        }
        .card-Item:not(.view-list) {
            flex: 100%;
            max-width: 100%;
        }
        .card-img {
            height: 190px;
        }
    }
}
