            }
            .tab-containers[data-tab="card-tab"] {
                .tab-container:first-child {
                    display: none !important;
                }
                .tab-container:last-child {
                    display: block !important;
                }
            }
            .sorting__wrap {
                margin-bottom: 20px;
            }
        }
    }
}
@media(max-width: 575px) {
    .card-product {
        &__tabslider,&__tabslider1 {
            .card-full {
                & .card-link {
                    height: auto;
                    overflow: visible;
                }
            }
        }
    }
}
@media(max-width: 400px) {
    .card-product {
        &__slider {
            .gallery-top {
                height: 164px;
            }
        }
        &__action {
            &-delivery {
                &>span {
                    display: flex;
                    flex-wrap: wrap;
                }
                .icon-6-7_delivery {
                    width: 100%;
                    margin: 0 0 5px;
                }
            }
            &-otherbtn {
                flex-wrap: wrap;
                button {
                    width: 100%;
                    &:last-child {
                        margin-top: 15px;
                    }
                }
            }
        }
        &__page {
            .card-Item[data-card="list"] {
                .card-img  {
                    height: 165px;
                }
                .card-body .card-action {
                    top: 195px;
                }
            }
        }
    }
}